.app__aboutus {
    position: relative;
}

.app__aboutus-overlay {
    position: absolute;
    inset: 0;
}

.app__aboutus-overlay img {
    width: 700px;
    height: 700px;
    z-index: 0;
}

.app__aboutus-content {
    width: 100%;
    z-index: 2;
}

.app__aboutus-content_about {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: right;
}

.app__aboutus-content_drink {
    margin: 2rem 4rem;
}

.app__aboutus-content_drink img {
    height: 700px;
}

.app__aboutus-content_ourstory {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    text-align: left;
}

.app__aboutus-content_ourstory p,
.app__aboutus-content_about p{
    margin: 2rem 0;
    color: var(--color-grey);
    text-transform: none;
}

@media screen and (min-width: 2000px) {

    .app__aboutus-content_ourstory p,
    .app__aboutus-content_about p {
        margin: 4rem 0;
    }
}

@media screen and (max-width: 900px) {
    .app__aboutus-content {
        flex-direction: column;
    }
    
    .app__aboutus-content_drink {
        margin: 4rem 0;
    }
}

@media screen and (max-width: 650px) {
    .app__aboutus-overlay img {
        width: 80%;
        height: 320px;
    }
    .app__aboutus-content_drink img{
        width: 100%;
        height: 280px;
    }
}
